.pickup-container {
    width: 100%;
    height: 100%;
}

.pickup-main {
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  margin: 1%;
  font-weight: bold;
  font-family: sans-serif;
}

.pickup-block1 {
  position: absolute;
  font-size: 6vh;
  width: 75%;
  height: 21%;
  top: 0;
  left: 0;
}

.pickup-1-in {
  height: 50%;
  background-color: darkblue;
  color: white;
  height: 50%;
  width: 100%;
  margin: 0 auto;
  border-radius: 5px;
  vertical-align: text-top;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.pickup-block2 {
  position: absolute;
  font-size: 3vh;
  width: 23%;
  height: 96%;
  bottom: 4%;
  right: 0;
  border-left: 0.08em solid #da8418;
}

.pickup-header-right {
  border-bottom: 0.15em solid darkblue;
  width: 80%;
  text-align: center;
  border-bottom-color: darkblue;
  padding-top: 4%;
  padding-bottom: 4%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 4%;
}

.pickup-dotted-div-right {
  border-bottom: 0.15em dashed darkblue;
  width: 80%;
  text-align: center;
  border-bottom-color: darkblue;
  color: #d10808;
  padding-top: 4%;
  padding-bottom: 4%;
  margin-left: auto;
  margin-right: auto;
}

.pickup-block3 {
  position: absolute;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  font-size: 6vh;
  width: 75%;
  height: 75%;
  bottom: 4%;
  left: 0;
}

.pickup-block3-cel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  vertical-align: middle;
  height: 100%;
}

.pickup-block3-cont {
  background-color: #fff;
  width: 33%;
  height: 33%;
  text-align: center;
}

.pickup-grid-cel-1 {
  border-right: 0.08em solid #da8418;
}
.pickup-grid-cel-2 {
}
.pickup-grid-cel-3 {
  border-left: 0.08em solid #da8418;
}
.pickup-grid-cel-4 {
  border-top: 0.08em solid #da8418;
  border-bottom: 0.08em solid #da8418;
}
.pickup-grid-cel-5 {
  border: 0.08em solid #da8418;
}
.pickup-grid-cel-6 {
  border-top: 0.08em solid #da8418;
  border-bottom: 0.08em solid #da8418;
}
.pickup-grid-cel-7 {
  border-right: 0.08em solid #da8418;
}
.pickup-grid-cel-8 {
}
.pickup-grid-cel-9 {
  border-left: 0.08em solid #da8418;
}




